<template>
  <!-- Creates a navigation drawer, which opens and closes depending on the size of the screen you're looking at. -->
  <v-navigation-drawer
    id="app-drawer"
    v-model="inputValue"
    app
    dark
    floating
    persistent
    mobile-break-point="991"
    width="260"
  >
    <!-- Takes the background image. -->
    <v-img
      :src="image"
      height="100%"
    >
      <v-layout
        class="fill-height"
        tag="v-list"
        column
      >
        <v-list-tile avatar>
          <v-list-tile-avatar
            color="white"
          >
            <!-- Sets the logo, along with the name, at the top of the navigation drawer. -->
            <v-img
              :src="logo"
              height="45"
              contain
            />
          </v-list-tile-avatar>
          <v-list-tile-title class="title">
            Wei Trinh
          </v-list-tile-title>
        </v-list-tile>
        <v-divider />
        <!-- Creates a link to each of the subpages. -->
        <v-list-tile
          v-for="(link, i) in links"
          :key="i"
          :to="link.to"
          :active-class="color"
          avatar
          class="v-list-item"
        >
          <!-- Each link has its own icon and text, and those are used here. -->
          <v-list-tile-action>
            <v-icon>{{ link.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-title
            v-text="link.text"
          />
        </v-list-tile>
        <!-- A night mode switcher, which is only on the navigation drawer.-->
        <v-list-tile
          class="v-list-item v-list__tile--buy"
        >
          <v-switch
            :label="'Dark Theme'"
            @click.native="switchDark"
          />
        </v-list-tile>
      </v-layout>
    </v-img>
  </v-navigation-drawer>
</template>

<script>
import EventBus from '../../eventBus'
// Utilities
import {
  mapMutations,
  mapState
} from 'vuex'

export default {
  data () {
    return {
      // Defines all of our logos and links.
      logo: './img/vuetifylogo.png',
      links: [
        {
          to: '/',
          icon: 'mdi-view-dashboard',
          text: 'Home Page'
        },
        {
          to: '/current-research',
          icon: 'mdi-account',
          text: 'Current Research'
        },
        {
          to: '/team-overbye',
          icon: 'mdi-account-group',
          text: 'Team Overbye'
        },
        {
          to: '/contours',
          icon: 'mdi-animation',
          text: 'Contours'
        },
        {
          to: '/power-market',
          icon: 'mdi-finance',
          text: 'Power Market'
        },
        {
          to: '/tpec',
          icon: 'mdi-flash',
          text: 'TPEC 2020'
        },
        {
          to: '/photography',
          icon: 'mdi-camera',
          text: 'Photography'
        }
      ],
      responsive: false,
      dark: false
    }
  },
  computed: {
    ...mapState('app', ['image', 'color']),
    inputValue: {
      get () {
        return this.$store.state.app.drawer
      },
      set (val) {
        this.setDrawer(val)
      }
    },
    items () {
      return this.$t('Layout.View.items')
    }
  },
  // I think these are used for the resizer.
  mounted () {
    this.onResponsiveInverted()
    window.addEventListener('resize', this.onResponsiveInverted)
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.onResponsiveInverted)
  },
  methods: {
    // We emit to an EventBus, which is used by App.vue. We have to send something over,
    // so I just sent a variable this.dark over, but it's never used.
    switchDark () {
      EventBus.$emit('switch-dark', this.dark)
    },
    // This function is here so that we get a constant menu if the screen width is less than 991px.
    ...mapMutations('app', ['setDrawer', 'toggleDrawer']),
    onResponsiveInverted () {
      if (window.innerWidth < 991) {
        this.responsive = true
      } else {
        this.responsive = false
      }
    }
  }
}
</script>

<style lang="scss">
  #app-drawer {
    .v-list__tile {
      border-radius: 4px;

      &--buy {
        margin-top: auto;
        margin-bottom: 17px;
      }
    }

    .v-image__image--contain {
      top: 9px;
      height: 60%;
    }

    .search-input {
      margin-bottom: 30px !important;
      padding-left: 15px;
      padding-right: 15px;
    }
  }
</style>
