<script>
import EventBus from './eventBus'
export default {
  data: () => ({
    // Initializing the isDark option that lets us switch between dark and light mode.
    isDark: false
  }),
  mounted () {
    // The EventBus is also used by the Drawer.vue. Think of it as a subscribe/publish, where
    // 'switch-dark' is the topic you're subscribing to.
    EventBus.$on('switch-dark', (dark) => {
      this.isDark = !this.isDark
    })
  }
}
</script>

