/**
 * Vue i18n
 *
 * @library
 *
 * http://kazupon.github.io/vue-i18n/en/
 */

// Lib imports
// Importing font-awesome fonts.
import '@fortawesome/fontawesome-free/css/all.css'
import Vue from 'vue'
import VueI18n from 'vue-i18n'
import messages from '@/lang'

Vue.use(VueI18n)

const i18n = new VueI18n({
  locale: 'en',
  fallbackLocale: 'en',
  messages,
  // Added this in to include more iconfonts.
  iconfont: 'fa'
})

export default i18n
